import Vue from 'vue';
import moment from 'moment';

import App from './App.vue';
import router from './router';
import store from './store/index';

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app');

Vue.filter('time', value => value && moment(String(value)).format('hh:mm'));
